.Comments-list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 5px 10px 10px 5px;
    background: rgb(236, 236, 236);
}

.Comments-list-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    padding: 2px 2px;
    margin-top: 1px;
    margin-bottom: 1px;
}

.Comments-list-item-user {
    color: #000;
    font-weight: bold;
    font-size: 14px;
    padding: 0 1px;
}

.Comments-list-item-date {
    -ms-flex-item-align: end;
        align-self: flex-end;
    color: darkgray;
    font-size: 12px;
    padding: 0 1px;
}

.Comments-list-item-comment {
    -ms-flex: 1 1;
        flex: 1 1;
    padding: 0 1px 1px 1px;
    margin-left: 2px;
    text-align: start;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}


