.News-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    min-height: 100vh;
    padding: 2px 2px 2px 2px;
}

.News-item-back {
    -ms-flex-item-align: start;
        align-self: flex-start;
    /* width: 100%;
    text-align: start; */
    margin-left: 12px;
    color: green;
}

.News-item-title {
    font-size: 18px;
    font-weight: bold;
    color: #000;
    margin: 5px 10px 10px 2px;
}

.News-item-title-en {
    font-size: 16px;
    font-weight: bold;
    color: #000;
    margin: 3px 5px 5px 5px;
}

.News-item-from {
    font-size: 12px;
    color: #000;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.News-item-date {
    font-size: 12px;
    color: #000;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.News-item-abstract {
    width: 100%;
    margin: 5px 10px 10px 5px;
    font-size: 12px;
    color: #000;
}

.News-item-abstract-en {
    width: 100%;
    margin: 0px 10px 10px 5px;
    font-size: 12px;
    color: #000;
}


